import React, { Component } from 'react';
import { getBook } from '../../Redux/actions';
import { connect } from 'react-redux';

import Typography from '@material-ui/core/Typography';
import Grid from '@material-ui/core/Grid';


import Card from '@material-ui/core/Card';
import CardActionArea from '@material-ui/core/CardActionArea';
import CardContent from '@material-ui/core/CardContent';
import CardMedia from '@material-ui/core/CardMedia';
import Hidden from '@material-ui/core/Hidden';

import LinearProgress from '@material-ui/core/LinearProgress';


import Paper from '@material-ui/core/Paper';


import { styled } from '@material-ui/styles';

import LinesEllipsis from 'react-lines-ellipsis'

const MyPaper = styled(Paper)({
    position: 'relative',
    color: '#fff',
    marginBottom: 8,
    backgroundImage: 'url(https://source.unsplash.com/user/erondu)',
    backgroundSize: 'cover',
    backgroundRepeat: 'no-repeat',
    backgroundPosition: 'center',
});



class Home extends Component  {
   constructor(props) {
    super(props);
    this.state = {isToggleOn: true};

    // This binding is necessary to make `this` work in the callback
    this.handleClick = this.handleClick.bind(this);
    }
    componentDidMount(){
        this.props.onBookNew()
    }

    _goToBookDetail = (EventId) =>{ 
        //alert(EventId)
        //this.props.goToScreen('BookFreeDetail',EventId);
    }

    handleClick= (EventId) =>{
      alert(EventId)
    }
    render() {

    if(this.props.isFetching){
           return (
                <div style={{ flexGrow: 1}} >

                <LinearProgress />
                <br />
                <LinearProgress color="secondary" />
                
              </div>
            );
          }
      
      return (
     
        <div>
        <MyPaper>
            
              <img
                style={{ display: 'none' }}
                src="https://source.unsplash.com/user/erondu"
                alt="background"
              />
            
            <div style={{position: 'absolute',
                    top: 0,
                    bottom: 0,
                    right: 0,
                    left: 0,
                    backgroundColor: 'rgba(0,0,0,.3)' }} />
            <Grid container>
              <Grid item md={6}>
                <div style={{position: 'relative',padding: 32
                     }}>
                  <Typography component="h1" variant="h3" color="inherit" gutterBottom>
                    “I think being in love with life is a key to eternal youth.”
                  </Typography>
                  <Typography variant="h5" color="inherit" paragraph>
                    Multiple lines of text that form the lede, informing new readers quickly and
                    efficiently about what&apos;s most interesting in this post&apos;s contents.
                  </Typography>
                 
                </div>
              </Grid>
            </Grid>
          </MyPaper>
        <div>

          <Grid container spacing={6}>
            {this.props.bookNew.map(item => (
              <Grid item key={item._id} xs={12} md={6}>
                <CardActionArea component="a" href="#">
                  <Card style={{ display: 'flex'}}>
                    <div style={{ flex: 1}}>
                      <CardContent>
                        <div >
                          <LinesEllipsis
                             text={item.name}
                             maxLine='1'
                             ellipsis='...'
                             trimRight
                              basedOn='letters'
                                />
                            </div>
                                             
                            <div >
                                              
                             <LinesEllipsis
                                text={item.author}
                                maxLine='1'
                                 ellipsis='...'
                                 trimRight
                              basedOn='letters'
                             />
                            </div>
                              <div>
                             <LinesEllipsis
                                 text={item.descript}
                                  maxLine='4'
                                   ellipsis='...'
                                   trimRight
                                   basedOn='letters'
                              />
                            </div>
                      </CardContent>
                    </div>
                    <Hidden xsDown>
                      <CardMedia
                        style={{ width: 160, height: 230}}
                        image={item.productImage}
                        title="Image title"
                      />
                    </Hidden>
                  </Card>
                </CardActionArea>
              </Grid>
            ))}
          </Grid>
        </div>
       </div>
        
    );
    }
}

function mapStateToProps  (state)  {
    return {
      bookNew: state.productBook.productDataBookNew,
       isFetching: state.productBook.isFetching
    };
}

function mapDispatchToProps (dispatch) {
    return {
        onBookNew:() =>dispatch(getBook.fetchDataFromProductBookNew()) 
    }
}
export default connect (mapStateToProps, mapDispatchToProps)(Home)


